import React from 'react';
import ShowImage from './ShowImage';

class App extends React.Component {

    state = {allImages: [], image: ''};

    importAll = (r) => {
        return r.keys().map(r);
    }

    componentDidMount() {
        const allImages = this.importAll(require.context('../img', false, /\.(png|jpe?g|svg)$/));
        this.setState({allImages: allImages});
    }

    changeImage = () => {
        const index = Math.floor(Math.random() * this.state.allImages.length);
        this.setState({image: this.state.allImages[index]});
    }

    render() {
        const videoSrc = 'https://www.youtube.com/embed/B3zTLvixqZY';
        const index = Math.floor(Math.random() * this.state.allImages.length);

        return (
            <div className="ui container">
                <h1 style={{textAlign: 'center', marginTop: '10px'}}>This BNK48 Does Not Exist</h1>
                <div style={{marginLeft: '10px', marginRight: '10px'}}>
                    Produced by Deep Learning GAN (Generative Adversarial Network) <a target="_blank" href="https://arxiv.org/abs/1812.04948" rel="noopener noreferrer">StyleGan</a> (paper).
                    Click next for a random StyleGAN generated. These faces were generated with =0.7, 0.8 and 0.9 hyperparameters.
                    0.9 batch are more diversely but less quality.
                </div>
                <ShowImage image={this.state.allImages[index]} />

                <div className="ui container" style={{textAlign: 'center'}}>
                    <div
                    onClick={this.changeImage}
                    className="ui animated button" tabIndex="0"
                    style={{marginTop: '15px'}}>
                        <div className="visible content">Next</div>
                        <div className="hidden content">
                            <i className="right arrow icon"></i>
                        </div>
                    </div>
                </div>

                <div style={{margin: '20px auto 0 auto', width: '300px', height: '200px', position: 'relative'}}>
                    <div className="ui embed">
                        <iframe
                            src={videoSrc}
                            allowFullScreen
                            title="GAN meet BNK"/>
                    </div>
                </div>


                <div className="ui inverted footer segment form-page">
                    <div className="ui container">
                        <div style={{textAlign: 'center'}}>
                             2019 OL. All Rights Reserved
                        </div>
                    </div>
                </div>
            </div>


        );
    }

}

export default App;
